import styled from "styled-components";
import Link from "next/link";

const Container = styled.div`
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 10px;
  margin: 0 auto 100px;
  max-width: 650px;
`;

const Button = styled.div`
  border: 1px solid yellow;
  text-align: center;
`;

const Tab = ({ text, i, ...props }) => (
  <Button
    onClick={i => {
      props.switchTab(i);
      console.log(i);
    }}
  >
    {text}
  </Button>
);

const TabBar = ({ tabs, ...props }) => (
  <Container>
    {tabs.map((t, i) => (
      <Tab text={t} i={i} switchTab={props.switchTab} key={t} />
    ))}
  </Container>
);

export default TabBar;
